body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#class{
  margin-top: 95px;
  position: absolute;
}

#links{
  width: 100px;
  display: display;
  justify-content: space-between;
}

#card{
  position: relative;
  width: 200px;
  height: 200px;
  flex: 0 0 33.333333%;
  text-align: center;
  padding: 30px;
}

/* #card:hover{

} */

.grow { 
  transition: all .2s ease-in-out; 
  }
  
  .grow:hover { 
  transform: scale(1.1); 
  }

#room-container{
  position: absolute;
  display: flex;
  margin-top: 90px;
  height: 900px;
  width: 75%;
  margin-left: 250px; 
  background-color: aquamarine;
  justify-content: space-around;
  flex-wrap: wrap;
  
}

#selected-room{
  display: block;
  margin-top: 100px;
  height:750px;
  width: 70%;
  margin-left: 350px; 
  background-color: aquamarine;
  text-align: center;

}

.rounded-t-5 {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

@media (min-width: 992px) {
  .rounded-tr-lg-0 {
    border-top-right-radius: 0;
  }

  .rounded-bl-lg-5 {
    border-bottom-left-radius: 0.5rem;
  }
}

.login-form{
  width: 60%;
  margin-left: 300px;
  margin-top: 200px;
}

#payment_token{
  width: 30%;
  margin-left: 450px;
}

#alert-message{
  width: 70%;
  height: 500px;
}

#confirm{
  text-align: center;
  align-items: center;
  margin-left: 450px;
  margin-top: 150px;
  
}

#room-image{
  height: 500px;
  width: 55%;
}

#home-body{
  height: 80%;
  width: 90%;
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/e/e8/Hostel_Dormitory.jpg");
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 100px;
  margin-top: 50px;
}

#welcome{
  height: 300px;
  width: 500px;
  background-color: aliceblue;
  opacity: 0.4;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.nav-item{
  width: 150px;
  display: flex;
  justify-content: space-between;
}